import jsPDF from "jspdf";
import "jspdf-autotable";

const appointmentDescription = {
  "week-14": "Ultraschall, wenn keine frühe Feindiagnostik erfolgt ist.",
  "week-16": "Besprechen Befund frühe Feindiagnostik",
  "week-20": "2. Ultraschall, ggf. Blutentnahme (Kontrolle Toxoplasmose/CMV, Bestimmung fetaler Rhesus-Faktor)",
  "week-25": "50g-Zuckertest, Blutentnahme (2. Antikörpertest, kleines Blutbild)",
  "week-30": "3. Ultraschall, Impfung Keuchhusten",
  "week-33": "CTG, Blutentnahme (Hepatitis B, kleines Blutbild, ggf. Kontrolle Toxoplasmose/CMV)",
  "week-36": "CTG, Ultraschall Wachstumskontrolle, ggf. B-Streptokokken-Abstrich",
  "week-37": "CTG, Geburtsplanung",
  "week-38": "CTG, Geburtsplanung",
  "week-39": "CTG, Geburtsplanung",
  "birthdate": "CTG, Ultraschall Fruchtwasser"
};

// define a generatePDF function that accepts appointments
const generatePDF = (pregnancy, appointments) => {
  // initialize jsPDF
  const doc = new jsPDF();

  // define the columns we want and their titles
  const tableColumn = ["Wann", "Termin", "Was wird gemacht?"];

  const tableRows = appointments.map(appointment => [
      appointment.readableId + "\n(" + appointment.description + ")",
      "",
      appointmentDescription[appointment.id] ?? ""
    ]);


  // startY is basically margin-top
  doc.autoTable(tableColumn, 
    tableRows, 
    {
      startY: 50,
      headStyles :{textColor: [255, 255, 255],fillColor : [77, 148, 160]}, // table header color: #4d94a0
      theme: 'grid',
      columnStyles: {
          0: {
                cellWidth: 50,
              },
          1: {
                cellWidth: 50,

             },
          2: {
                cellWidth: 80,
             }
      }
    });

  doc.setFontSize(30);
  doc.text("Schwangerschaftsplaner", 15, 18);
  doc.setFontSize(16);
  doc.text(["Erster Tag letzte Regel: " + pregnancy.periodDate, 
    "Korrektur: " + pregnancy.periodAdjustment + " Tage", 
    "Errechneter Geburtstermin: " + pregnancy.birthDate], 15, 28);
  doc.setProperties({
    title: "Schwangerschaftsplaner"
});

    //   const imgData = 'data:image/jpeg;base64,' + Base64.encode('public/logo192.png');
    // console.log(imgData);
    // doc.addImage(imgData, 'JPEG', 15, 40, 180, 160);


// var myImage = new Image();
// myImage .src = 'https://www.myserver.com/somepath/myImage.png';
// myImage .onload = function(){
// doc.addImage(myImage , 'png', 5, 5, 40, 10);
// doc.save('myPDF.pdf');
// };

  const img = new Image();
 // img.crossOrigin = "";  `${process.env.PUBLIC_URL}/signin`
 // img.src = "pregnancy/logo-praxis-nova.png";
 img.src = `${process.env.PUBLIC_URL}/logo-praxis-nova.png`;
 console.log(img.src);
  doc.addImage(img, 'PNG', 145, 10, 35, 35);
  doc.setFontSize(9);
  doc.text(["Gyn-Praxis Nova, www.praxisnova-berlin.de"], 70, 280);
  // doc.text("Gyn-Praxis Nova", 170, 36);

//   const textField = new TextField();
// textField.Rect = [170, 35, 30, 10];
// textField.multiline = true;
// textField.value = "Gyn-Praxis Nova https://praxisnova-berlin.de/";
// textField.fieldName = "TestTextBox";

// //SET FONT SIZE
// textField.maxFontSize = 9;

// doc.addField(textField);




  window.open(doc.output('bloburl'));

};

export default generatePDF;
